import { SQLite, SQLiteObject } from "@ionic-native/sqlite";

//date format:YYYYMMDD
const SQL_CREATE_SNAPSHOT = 'CREATE TABLE IF NOT EXISTS snapshot' +
  '(date TEXT PRIMARY KEY, amount INTEGER)';
const SQL_CREATE_ITEM = 'CREATE TABLE IF NOT EXISTS item' +
  '(date TEXT, no INTEGER,  platform:TEXT, risk:TEXT, term:TEXT, name:TEXT, amount:INTEGER, PRIMARY KEY(date, no))';

export class DBService {
  private db: SQLiteObject;

  constructor(private engine: SQLite) {
    this.engine.create({ name: 'db', location: 'default' })
      .then((dbObj: SQLiteObject) => {
        this.db = dbObj;

        this.db.executeSql(SQL_CREATE_SNAPSHOT, {});
        this.db.executeSql(SQL_CREATE_ITEM, {});
      });
  }
}


